'use client';
import { useState, useEffect } from 'react';
import styles from '../../styles/Dashboard.module.css';

export default function Dashboard() {
    const [user, setUser] = useState(null);

    useEffect(() => {
        // Fetch user data
        const fetchUser = async () => {
            try {
                const res = await fetch('/api/user');
                if (res.ok) {
                    const data = await res.json();
                    setUser(data);
                } else {
                    throw new Error('Failed to fetch');
                }
            } catch (e) {
                // Fallback data
                setUser({
                    name: 'Elon Musk',
                    totalInvested: 15000,
                    currentValue: 16250,
                    activePlans: [
                        {
                            id: 1,
                            planId: 2,
                            name: 'Cybertruck Growth',
                            investedAmount: 10000,
                            currentValue: 10800,
                            dateInvested: '2023-01-15'
                        },
                        {
                            id: 2,
                            planId: 4,
                            name: 'Autonomy AI',
                            investedAmount: 5000,
                            currentValue: 5450,
                            dateInvested: '2023-03-10'
                        }
                    ]
                });
            }
        };

        fetchUser();
    }, []);

    if (!user) return <div className={styles.loading}>Loading...</div>;

    const totalReturn = user.currentValue - user.totalInvested;
    const returnPercentage = ((totalReturn / user.totalInvested) * 100).toFixed(2);

    return (
        <div className={styles.dashboard}>
            <h1 className={styles.title}>Welcome back, {user.name}</h1>

            <div className={styles.overview}>
                <div className={styles.card}>
                    <h3>Total Invested</h3>
                    <p className={styles.amount}>${user.totalInvested.toLocaleString()}</p>
                </div>
                <div className={styles.card}>
                    <h3>Current Value</h3>
                    <p className={styles.amount}>${user.currentValue.toLocaleString()}</p>
                </div>
                <div className={styles.card}>
                    <h3>Total Return</h3>
                    <p className={`${styles.amount} ${totalReturn >= 0 ? styles.positive : styles.negative}`}>
                        {totalReturn >= 0 ? '+' : ''}${totalReturn.toLocaleString()} ({returnPercentage}%)
                    </p>
                </div>
            </div>

            <h2 className={styles.subtitle}>Your Portfolio</h2>
            <div className={styles.portfolio}>
                {user.activePlans.map(plan => (
                    <div key={plan.id} className={styles.planRow}>
                        <div className={styles.planInfo}>
                            <h4>{plan.name}</h4>
                            <span className={styles.date}>Invested on {plan.dateInvested}</span>
                        </div>
                        <div className={styles.planStats}>
                            <div className={styles.statGroup}>
                                <span className={styles.label}>Invested</span>
                                <span>${plan.investedAmount.toLocaleString()}</span>
                            </div>
                            <div className={styles.statGroup}>
                                <span className={styles.label}>Current Value</span>
                                <span>${plan.currentValue.toLocaleString()}</span>
                            </div>
                            <div className={styles.statGroup}>
                                <span className={styles.label}>Return</span>
                                <span className={plan.currentValue >= plan.investedAmount ? styles.positive : styles.negative}>
                                    {((plan.currentValue - plan.investedAmount) / plan.investedAmount * 100).toFixed(2)}%
                                </span>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
}
